function catalogSlider() {
    var swiper = new Swiper('.catalog .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        navigation: {
            nextEl: '.catalog .swiper-button-next',
            prevEl: '.catalog .swiper-button-prev',
        },
    })
}

function reviewSlider() {
    var swiper = new Swiper('.review .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: '.review .swiper-button-next',
            prevEl: '.review .swiper-button-prev',
        },
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        spaceBetween: 0,
        slidesPerView: 3,
        freeMode: true,
        watchSlidesProgress: true,
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 0,
        slidesPerView: "auto",
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: '.gallery .swiper-button-next',
            prevEl: '.gallery .swiper-button-prev',
        },
    });
}

catalogSlider()
reviewSlider()
gallery()
